body {
    position: relative;
    margin-top: $header-height;
    background-color: $white;
    overflow-x: hidden;
    font-family: $f-open;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: $f-montserrat;
    margin: 0;
    padding: 0;
}

figure,
ol,
ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

a {
    color: $black;
    text-decoration: none;
}

img {
    max-width: 100%;
    height: auto;
}

.section-title {
    font-weight: 100;
    font-size: 40px;
    margin-bottom: 55px;
    text-align: center;
    letter-spacing: 6px;
}

.bold {
    font-weight: bold;
}

.spacer {
    display: inline-block;
    height: 100%;
    min-height: 10px;
    width: 1px;
    margin: 0 3px 0 5px;
    background-color: $c-grey;
}
.btn {
    border: none;
    background: transparent;

    &:focus {
        outline: none;
    }
}


.no-scroll {
    height: 100%;
    overflow: hidden;
}

// Header Text hide
.hide-text {
    display: inline-block;
    opacity: 1;
    transition: all 0.3s ease-in;
    &.active {
        width: 0%;
        opacity: 0;
    };
}


.logo {
    font-family: $f-mali;
    letter-spacing: 4px;
    margin: 0 auto;
}

.thumbnail {
	position: relative;
	display: inline-block;
	overflow: hidden;

	img {
		object-fit: cover;
		width: 100%;
		height: 100%;
	}
}


.form-group {
    position: relative;
}

.post__title {
    font-size: 50px;
    font-weight: 100;
}

.grey-filter {
    position: relative;
    &:after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: $c-grey07;
        // background: $bg-gradient;
    }
}

.gradient-bg {
    position: relative;
    &:after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgb(0,0,0);
        background: $bg-gradient;
    }
}

.spinner-loader {
    margin: $gapMedium*2 auto 0 auto;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    border: 0.25rem solid rgba(0, 0, 0, 0.2);
    border-top-color: black;
    animation: spin 1s infinite linear;
}

